import { Storage } from '@ionic/storage';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class ConfigsProvider {

  public apiEndpoint: string = "http://api.leptons.com:84/api/";

  //http://18.228.157.163:84/api/devices
  constructor(public http: HttpClient, private storage: Storage) {
    this.storage.keys()
      .then((keys) => {
        if(keys.indexOf('api_endpoint') >= 0){
          this.storage.get('api_endpoint')
            .then((url) => {
              this.apiEndpoint = url;
            });
        }

      })
      .catch(() => {
        this.apiEndpoint = "http://api.leptons.com:84/api/";
      });
  }

}



