import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ConfigsProvider } from '../configs/configs';
import { LampCommand } from '../../app/models/LampCommand';

@Injectable()
export class LampsProvider {

  constructor(public http: HttpClient, private configs: ConfigsProvider) {
    
  }

  public turnOn(command: LampCommand) : Promise<boolean>{
    const headers = new HttpHeaders({ });
    const url = this.configs.apiEndpoint + "devices/" + command.device_id + "/commands";
    return this.http.post<LampCommand>(url, command, {
      headers: headers
    }).toPromise()
      .then((res) => {
        command.id = res.id;
        return this.waitCommandBeExecuted(command)
          .then((r) => {
            command.pending = false;
            return r;
          });
      })
      .catch((err) => {
        return false;
      });
  }

  public turnOff(command: LampCommand) : Promise<boolean>{
    const headers = new HttpHeaders({ });
    const url = this.configs.apiEndpoint + "devices/" + command.device_id + "/commands";
    return this.http.post<any>(url, command, {
      headers: headers
    }).toPromise()
    .then((res) => {
      command.id = res.id;
      return this.waitCommandBeExecuted(command)
      .then((r) => {
        command.pending = false;
        return r;
      });
    })
    .catch((err) => {
      return false;
    });
  }

  public waitCommandBeExecuted(command: LampCommand) : Promise<boolean>{
    const headers = new HttpHeaders({ });

    const url = this.configs.apiEndpoint +  "devices/" + command.device_id + "/commands/" + command.id;
    return this.http.get<any>(url, {
      headers: headers
    }).toPromise()
      .then((result) => {
        if(!result.pending){          
          return true;
        }

        let promise = new Promise<boolean>((resolve, reject) => {
          setTimeout(() => {
            this.waitCommandBeExecuted(command).then((a) => {
              resolve(a);
            });            
          }, 1000);
        });

        return promise;
      })
      .catch((err) => {
        return false;
      });
  }
}



