import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Events } from 'ionic-angular';
import { DevicesProvider } from '../../providers/devices/devices';
import { PageBase } from '../PageBase';
import { ConfigsProvider } from '../../providers/configs/configs';

@IonicPage()
@Component({
  selector: 'page-devices',
  templateUrl: 'devices.html',
})
export class DevicesPage extends PageBase{

  private activeDeviceList : Array<any>;
  private inactiveDeviceList : Array<any>;

  public static firstLoadedDevices:Array<any> = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    private devicesProvider: DevicesProvider, protected loadingCtrl: LoadingController,
    private events: Events, private configs: ConfigsProvider) {
    super(loadingCtrl);
  }

  ionViewDidLoad() {
    this.loadDevices();
  }

  private loadDevices() {
    this.setPageLoading(true);
    this.devicesProvider.getDevices()
      .then((dev) => {
        this.activeDeviceList = dev.filter((d) => d.active);
        this.inactiveDeviceList = dev.filter((d) => !d.active);
        this.setPageLoading(false);
        DevicesPage.firstLoadedDevices = dev;
        this.events.publish("devicesLoaded", dev);
      });
  }

  private inactivateDevice(device : any){
    this.loadDevices();
  }


  private activateDevice(device : any){
    this.loadDevices();
  }  
}



