import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class ActionProvider {

  constructor(public http: HttpClient) {
    
  }

  public turnAcOn(){
    this.createAction('TurnOnAC');
  }

  public turnAcOff(){
    this.createAction('TurnOffAC');
  }

  public openGate(){
    this.createAction('OpenGate');
  }

  public closeGate(){
    this.createAction('CloseGate');
  }

  public turnOnLight(){
    this.createAction('TurnOnLight');
  }

  public turnOffLight(){
    this.createAction('TurnOffLight');
  }

  private createAction(action:String){
      const url = "http://192.168.0.32:84/api/actions";
      let body = {
        'action': action
      };
  
      return this.http.post(url, body).subscribe((obj) => {
        
      });
  }

}



