import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';
import { MyApp } from './app.component';

import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ActionProvider } from '../providers/action/action';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { DevicesPage } from '../pages/devices/devices';
import { LampsPage } from '../pages/lamps/lamps';
import { ConfigsPage } from '../pages/configs/configs';
import { DevicesProvider } from '../providers/devices/devices';
import { ConfigsProvider } from '../providers/configs/configs';
import { LampsProvider } from '../providers/lamps/lamps';
import { Storage } from '@ionic/storage';

@NgModule({
  declarations: [
    MyApp,
    DevicesPage,
    LampsPage,
    ConfigsPage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    DevicesPage,
    LampsPage,
    ConfigsPage,
    TabsPage
  ],
  providers: [
    HttpClient,
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ActionProvider,
    DevicesProvider,
    ConfigsProvider,
    LampsProvider
  ]
})
export class AppModule {}



