import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ConfigsProvider } from '../configs/configs';

@Injectable()
export class DevicesProvider {

  constructor(public http: HttpClient, public configs: ConfigsProvider) {
    
  }

  public getDevices() : Promise<any>{
    const headers = new HttpHeaders({
      
    });

    const url = this.configs.apiEndpoint + "devices";
    return this.http.get<any>(url, {
      headers: headers
    }).toPromise();
  }

  public updateDevice(device : any): Promise<any>{
    const headers = new HttpHeaders({
      
    });

    const url = this.configs.apiEndpoint + "devices/" + device.id;
    return this.http.put<any>(url, device, {
      headers: headers
    }).toPromise();
  }

}


