import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, LoadingController } from 'ionic-angular';
import { DevicesPage } from '../devices/devices';
import { LampCommand } from '../../app/models/LampCommand';
import { LampsProvider } from '../../providers/lamps/lamps';
import { PageBase } from '../PageBase';

@IonicPage()
@Component({
  selector: 'page-lamps',
  templateUrl: 'lamps.html',
})
export class LampsPage extends PageBase{

  private devices: Array<any> = [];
  private deviceLamps: Array<DeviceLamps> = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private events: Events,
    private lampProvider: LampsProvider, protected loadingCtrl: LoadingController) {
    super(loadingCtrl);
    this.devices = DevicesPage.firstLoadedDevices;
  }

  ionViewDidLoad() {
    this.buildItems();

    this.events.subscribe('devicesLoaded', (dev) => {
      this.devices = dev;
      this.buildItems();
    });
  }

  private invert(lamp: Lamp){
    if(!lamp.canBeActivated){
      alert('Esta porta nÃ£o pode ser ativada.');
      return;
    }

    if(lamp.on){
      this.turnOff(lamp);
    }
    else{
      this.turnOn(lamp);
    }
  }

  private turnOn(lamp: Lamp){
    let c = new LampCommand();
    c.action = 1;//activation
    c.type = 0;//digital
    c.pin = lamp.pin;
    c.name = "TurnOnLamp";
    c.device_id = lamp.device_id;
    this.setPageLoading(true, "Aguardando o comando ser executado...");

    this.lampProvider.turnOn(c)
      .then((result) => {
        if(c.pending == false){
          lamp.on = true;
        }
      })
      .catch((err) => {

      })
      .then(()=>{
        this.setPageLoading(false);
      });
  }

  private turnOff(lamp: Lamp){
    let c = new LampCommand();
    c.action = 0;//deactivation
    c.type = 0;//digital
    c.pin = lamp.pin;
    c.name = "TurnOffLamp";
    c.device_id = lamp.device_id;
    this.setPageLoading(true, "Aguardando o comando ser executado...");

    this.lampProvider.turnOff(c)
      .then((result) => {
        if(c.pending == false){
          lamp.on  = false;
        };
      })
      .catch((err) => {

      })
      .then(()=>{
        this.setPageLoading(false);
      });
  }

  private buildItems(){
    if(this.devices == null){
      this.devices = [];
      return;
    }

    this.deviceLamps = [];
    this.devices.filter((d) => d.active).forEach((device) => {
      let dLamp =  new DeviceLamps();
      dLamp.device = device;

      for(let i = 2; i < 17; i++){
        if(i == 7 || i == 8 || i == 9 || i == 10 || i == 11 || i == 14 || i == 15){
          continue;
        }

        let l = new Lamp();
        l.device_Data = device;
        l.device_id = device.id;
        l.on = false;
        l.canBeActivated = (i == 5 || i == 16);
        l.pin = i;
        dLamp.lamps.push(l);
      }

      this.deviceLamps.push(dLamp);
    });
  }

}

class Lamp{
  public device_id: string;
  public device_Data : any;
  public pin : number;
  public canBeActivated: boolean;
  public on: boolean = false;
  public command: LampCommand;
}

class DeviceLamps{
  public lamps:Array<Lamp> = [];
  public device: any;
}


